//import react 
import React from 'react';
import { useEffect } from 'react';
import { useState } from 'react';

// import AWS
import Amplify, { API, graphqlOperation, Storage } from 'aws-amplify';
import awsconfig from '../../aws-exports';

//import GraphQl
import { listSongs } from '../../graphql/queries';
import { updateSong } from '../../graphql/mutations';

//import material
import { Paper, IconButton } from '@material-ui/core';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import FavoriteIcon from '@material-ui/icons/Favorite';
import PauseIcon from '@material-ui/icons/Pause';
import AddIcon from '@material-ui/icons/Add';

//import audio player
import ReactPlayer from 'react-player';

//import components
import AddSong from '../AddSong'


Amplify.configure(awsconfig);


const SongList = () => {
        //controll useState, Global Variables
        const [songs, setSongs] = useState([]);
        const [songPlaying, setSongPlaying] = useState('');
        const [audioURL, setAudioURL] = useState('');
        const [showAddSong, setShowAddSong] = useState(false);
    
        //Control useEffect 
        useEffect(() => {
            fetchSongs();
        }, []);
    
        const fetchSongs = async () => {
            try {
                const songData = await API.graphql(graphqlOperation(listSongs));
                const songList = songData.data.listSongs.items;
                console.log('song list', songList);
                setSongs(songList);
    
            } catch (error) {
                console.log('error on fetching songs', error);
            }
        };
    
        const addLike = async (idx) => {
            try {
                const song = songs[idx];
                song.like = song.like + 1;
                delete song.createdAt;
                delete song.updatedAt;
    
                const songData = await API.graphql(graphqlOperation(updateSong, { input: song }));
                const songList = [...songs];
                songList[idx] = songData.data.updateSong;
                setSongs(songList);
    
            } catch (error) {
                console.log('error on adding Like to song', error);
            }
        };
    
        const toggleSong = async (idx) => {
            if (songPlaying === idx){
                setSongPlaying('');
                return;
            }
            else {
                const songFilePath = songs[idx].filePath;
                try {
                    const fileAccessURL = await Storage.get(songFilePath, {expires: 60});
                    console.log("access URL", fileAccessURL);
                    setSongPlaying(idx);
                    setAudioURL(fileAccessURL);
                } 
                catch (error) {
                    console.log('error accessing the file from S3', error);
                    setSongPlaying('');
                    setAudioURL('');
                }
                return;
            }
        }

    return (
        <div className="songList">
            {songs.map((song, idx) => {
                return (
                    <Paper variant="outlined" elevation={2} key={`song${idx}`}>
                        <div className="songCard">
                            <IconButton aria-label="play" onClick={() => toggleSong(idx)}>
                                { songPlaying === idx ? <PauseIcon/> : <PlayArrowIcon />} 
                            </IconButton>

                            <div>
                                <div className="songTitle">{song.title}</div>
                                <div className="songOwner">{song.owner}</div>
                            </div>

                            <div>
                                <IconButton aria-label="like" onClick={() => addLike(idx)}>
                                    <FavoriteIcon />
                                </IconButton>
                                {song.like}
                            </div>

                            <div className="songDescription">{song.description}</div>

                        </div>

                        {
                            songPlaying === idx ? 
                            (
                                <div className='ourAudioPlayer'> 
                                    <ReactPlayer
                                        url = {audioURL}
                                        controls
                                        playing
                                        height="50px"
                                    
                                    />
                                </div>
                            )
                            :
                            null
                        }

                    </Paper>
                );
            })}
            {
                showAddSong ? (
                    <AddSong onUpload={() => {
                        setShowAddSong(false);
                        fetchSongs();
                    }}/>
                )
                :
                <IconButton onClick={() => setShowAddSong(true)}>
                    <AddIcon/>
                </IconButton> 
            }
                
        </div>
            
    )
}

export default SongList;